
import OFooter from "@/components/common/OFooter";
import OPulldownPullupList from "@/components/common/OPulldownPullupList";
import ViewBox from 'vux/src/components/view-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
OFooter, XButton, Flexbox, FlexboxItem,
OPulldownPullupList,
ViewBox
},
data() {
return {
bodyPaddingBottom: "55px",
pulldownPullupHeight: "-50px",
checkAllButtonText: "全选",
isCheckAllButton: true,
buttonIsShow: Boolean,
isButton: true,
pageType: {
typeValue: "",
typeName: "",
titleName: "",
ruleName: "",
linkName: ""
}
};
},
methods: {

onCheckAllRegisteredEvent() {
let oPanel = this.$refs.pulldownPullupList.$refs.oPanel
if (this.isCheckAllButton) {
oPanel.registeredEventCheckbox = []
for (let i = 0; i < oPanel.list.length; i++) {
oPanel.registeredEventCheckbox.push(oPanel.list[i].EventNo)
}
this.isCheckAllButton = false
this.checkAllButtonText = '取消'
}
else {
oPanel.registeredEventCheckbox = []
this.isCheckAllButton = true
this.checkAllButtonText = '全选'
}

},

onHandleRegisteredEvent() {
this.$refs.pulldownPullupList.$refs.oPanel.onHandleRegisteredEvent()
},
refreshData() {
this.checkAllButtonText = "全选"
this.isCheckAllButton = true
},//按钮是否显示
ifButton() {
this.isButton = false
}
},
mounted() {

},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.pageType.typeValue = vm.$route.query.typeValue;
vm.pageType.typeName = vm.$route.query.typeName;
vm.pageType.titleName = vm.$route.query.titleName;
vm.pageType.ruleName = vm.$route.query.ruleName;
vm.pageType.linkName = vm.$route.query.linkName;
if (vm.pageType.typeName === 'WAPR' && vm.pageType.typeValue == '4') {
vm.bodyPaddingBottom = "93px"
vm.pulldownPullupHeight = "-88px"
}
else {
vm.bodyPaddingBottom = "55px"
vm.pulldownPullupHeight = "-50px"
}
vm.$refs.pulldownPullupList.pageIndex = 0;
vm.$refs.pulldownPullupList.showloading = false;
vm.$refs.pulldownPullupList.list = [];
vm.$refs.pulldownPullupList.refreshing = true;
vm.$refs.pulldownPullupList.buttonIsShow = false;
vm.$refs.pulldownPullupList.isButton = true;
vm.$refs.pulldownPullupList.getList(() => { });
vm.$refs.pulldownPullupList.$refs.scroller.reset({ top: 0 });

vm.checkAllButtonText = "全选"
vm.isCheckAllButton = true
vm.$refs.pulldownPullupList.$refs.oPanel.registeredEventCheckbox = []
})
}
};



